body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {  
  
  font-family: sans-serif;
}
.app header, .app main {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto; 
}
div.app {  
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
header {
  flex-basis: 30vh;
  background-color: #000;
  color: white;
  text-align: center;
  line-height: 100%;
}
main {  
  display: flex;
  flex-grow: 1;
}
aside {
  flex-basis: 300px;
}
footer {
  background-color: #000;  
  color: white;
  text-align: center;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
nav {    
    margin-top: 30px;
    padding: 0 30px 30px 0;
}
ul {
    list-style: none;    
}
nav a {
    display: block;
    color: black;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: .3s;
}
nav a:hover {
    transform: translateX(10px);
}
nav a.active {
    transform: translateX(0);
    border-left: 5px solid gray;
}
nav li:nth-child(1) a.active {border-left-color:red;}
nav li:nth-child(2) a.active {border-left-color:blue;}
nav li:nth-child(3) a.active {border-left-color:green;}
nav li:nth-child(4) a.active {border-left-color:orange;}

header {
    position: relative;
    overflow: hidden;
}
header img {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    top:50%;
    left:50%;
    transform: translate(-50%,-60%);
}
footer {
    padding: 10px;
    text-transform: uppercase;
}
footer span {
    color: red;
}
.page {
    width: 100%
}
.contact h3 {
    margin-top: 30px;    
}
.contact textarea {    
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
}
.contact button {
    font-size: 17px;
    display: block;
    margin: 0 20% 0 auto;
    line-height: 200%;
    min-width: 100px;
    background-color: transparent;
    border: 2px solid black
}
.products > div:first-child {
    margin-bottom: 20px;
}
.products h2 {
    margin-bottom: 20px;
}
.products ul {
    list-style: square;
    padding-left: 20px;
}
.products li {
    margin-bottom: 20px;
}
.products a {
    display: block;
    max-width: 50px;
    text-decoration: none;
    color: #333;
    transition: .3s;
}
.products a:hover {
    transform: translateX(2px);
    color: black;
}
